@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
:root{
    --linear-default-bg: rgb(6,180,121);
    --linear-bg: linear-gradient(90deg, rgba(6,180,121,1) 0%, rgba(10,84,150,1) 100%); 
    --bg: #06284d;
    --text-white: white;
    --text-black: black;
}
.container{
    width: 94%;
    margin: 0 auto;
}
*{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
}
.loading_container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 180px 0 300px 0;
}
.placeholderImage{
    width: 130px;
}

.error_container{
    width: 100%;
    height: 100%;
}